import './App.css'; 

function Button(props) {
  const Assign=(x)=>{
      props.add(x);
  }
  const Delete=()=>{
    props.Del()
  }
  const Blank=()=>{
    props.Bla()
  }
  const Eval=()=>{
    props.Ans()
  }
  return (
    < div class="big1">
      <div class="B">
        <button  onClick={()=>Assign('(')} > ( </button> <button  onClick={()=>Assign(')')}>)</button><button onClick={()=>Blank()}>AC</button><button onClick={()=>Delete()}>CE</button>
      </div>
      <div class="B">
        <button onClick={()=>Assign('7')}>7</button><button onClick={()=>Assign('8')}>8</button><button onClick={()=>Assign('9')}>9</button><button onClick={()=>Assign('/')}>/</button>
      </div>
      <div class="B">
        <button onClick={()=>Assign('4')}>4</button><button onClick={()=>Assign('5')}>5</button><button onClick={()=>Assign('6')}>6</button><button onClick={()=>Assign('*')}>x</button>
      </div>
      <div class="B">
        <button onClick={()=>Assign('1')}>1</button><button onClick={()=>Assign('2')}>2</button><button onClick={()=>Assign('3')}>3</button><button onClick={()=>Assign('-')}>-</button>
      </div>
      <div class="B">
        <button onClick={()=>Assign('0')}>0</button><button onClick={()=>Assign('.')}>.</button><button onClick={()=>Eval()}>=</button><button onClick={()=>Assign('+')}>+</button>
      </div>
    </div>
  )
}

export default Button
