import React, { useState} from 'react';
import './App.css';
import Template from './Templates';
import Button from './Button';
function App() {
  const [val, setval]= useState([])
  const add=(x)=>{
    setval([...val, x]);
    
  }
  function Del(){
    let arr=[]
    for(let i=0; i<val.length-1; i++){
      arr.push(val[i])
    }
    setval(arr);

  }
  function Bla(){
    let arr=[]
    setval(arr)
  }
  function Ans(){
    let ans=''
    for(let i=0; i<val.length; i++){
      ans+=val[i];
    }
    if(eval(ans)!=console.error()){
    let a=[eval(ans)]
    setval(a)
    } else {
      let a=['Not Defined']
      setval(a)
    }
  }
  return (
    <>
      <h1>CALCULATOR</h1>
      <Template title={val}/>
      <Button Ans={Ans}Bla={Bla} Del={Del} add={add}/>
    </>
  );
}

export default App;
